// SendBird disconnect. Invalidates currentUser
// eslint-disable-next-line max-len
export const getConnect = (store) => (userId, accessToken) => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  if (!accessToken) {
    sdk.connect(userId, (response, error) => {
      const swapParams = sdk.getErrorFirstCallback();
      let res = response;
      let err = error;
      if (swapParams) {
        res = error;
        err = response;
      }
      if (err) {
        reject(err);
      } else {
        resolve(res);
      }
    });
  } else {
    sdk.connect(userId, accessToken, (response, error) => {
      const swapParams = sdk.getErrorFirstCallback();
      let res = response;
      let err = error;
      if (swapParams) {
        res = error;
        err = response;
      }
      if (err) {
        reject(err);
      } else {
        resolve(res);
      }
    });
  }
});

// SendBird disconnect. Invalidates currentUser
export const getDisconnect = (store) => () => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  sdk.disconnect((response, error) => {
    const swapParams = sdk.getErrorFirstCallback();
    let res = response;
    let err = error;
    if (swapParams) {
      res = error;
      err = response;
    }
    if (err) {
      reject(err);
    } else {
      resolve(res);
    }
  });
});

// Using the updateCurrentUserInfo() method
// you can update a user's nickname and profile image with a URL
// eslint-disable-next-line max-len
export const getUpdateUserInfo = (store) => (nickName, profileUrl) => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  sdk.updateCurrentUserInfo(nickName, profileUrl, (response, error) => {
    const swapParams = sdk.getErrorFirstCallback();
    let res = response;
    let err = error;
    if (swapParams) {
      res = error;
      err = response;
    }
    if (err) {
      reject(err);
    } else {
      resolve(res);
    }
  });
});

export default {
  getConnect,
  getDisconnect,
  getUpdateUserInfo,
};
