import Sb from 'sendbird';

import {
  INIT_SDK,
  SET_SDK_LOADING,
  RESET_SDK,
  SDK_ERROR,
} from './actionTypes';
import { INIT_USER, UPDATE_USER_INFO, RESET_USER } from '../user/actionTypes';

export const disconnectSdk = ({
  sdkDispatcher,
  userDispatcher,
  sdk,
  onDisconnect,
}) => {
  sdkDispatcher({ type: SET_SDK_LOADING, payload: true });
  if (sdk && sdk.disconnect) {
    sdk.disconnect((response, error) => {
      const swapParams = sdk.getErrorFirstCallback();
      let err = error;
      if (swapParams) {
        err = response;
      }
      if (!err) {
        sdkDispatcher({ type: RESET_SDK });
        userDispatcher({ type: RESET_USER });
      }
      onDisconnect();
    });
  } else {
    onDisconnect();
  }
};

export const handleConnection = ({
  userId,
  appId,
  nickname,
  profileUrl,
  accessToken,
  sdk,
}, dispatchers) => {
  const {
    sdkDispatcher,
    userDispatcher,
  } = dispatchers;
  disconnectSdk({
    sdkDispatcher,
    userDispatcher,
    sdk,
    onDisconnect: () => {
      sdkDispatcher({ type: SET_SDK_LOADING, payload: true });
      if (userId && appId) {
        const newSdk = new Sb({ appId });
        const connectCb = (response, error) => {
          const swapParams = newSdk.getErrorFirstCallback();
          let user = response;
          let err = error;
          if (swapParams) {
            user = error;
            err = response;
          }
          if (!err) {
            sdkDispatcher({ type: INIT_SDK, payload: newSdk });
            userDispatcher({ type: INIT_USER, payload: user });
            if (nickname || profileUrl) {
              const userInfoParams = [
                nickname || user.userId,
                profileUrl || user.profileUrl,
              ];
              newSdk.updateCurrentUserInfo(...userInfoParams, (r, e) => {
                let namedUser = r;
                let errorUpdatingInfo = e;
                if (swapParams) {
                  namedUser = e;
                  errorUpdatingInfo = r;
                }
                if (!errorUpdatingInfo) {
                  userDispatcher({ type: UPDATE_USER_INFO, payload: namedUser });
                }
              });
            }
          } else {
            sdkDispatcher({ type: RESET_SDK });
            sdkDispatcher({ type: RESET_USER });
            sdkDispatcher({ type: SDK_ERROR });
          }
        };
        if (accessToken) {
          newSdk.connect(userId, accessToken, connectCb);
        } else {
          newSdk.connect(userId, connectCb);
        }
      } else {
        sdkDispatcher({ type: SDK_ERROR });
      }
    },
  });
};
