import React, { useEffect, useReducer } from 'react';
import PropTypes from 'prop-types';

import { SendbirdSdkContext } from './SendbirdSdkContext';
import { handleConnection } from './dux/sdk/thunks';

import sdkReducers from './dux/sdk/reducers';
import userReducers from './dux/user/reducers';
import sdkInitialState from './dux/sdk/initialState';
import userInitialState from './dux/user/initialState';

export default function Sendbird(props) {
  const {
    userId,
    appId,
    accessToken,
    children,
    theme,
    nickname,
    profileUrl,
    userListQuery,
  } = props;

  const [sdkStore, sdkDispatcher] = useReducer(sdkReducers, sdkInitialState);
  const [userStore, userDispatcher] = useReducer(userReducers, userInitialState);

  useEffect(() => {
    // dispatch action
    handleConnection({
      userId,
      appId,
      accessToken,
      sdkStore,
      nickname,
      profileUrl,
      sdk: sdkStore.sdk,
    }, {
      sdkDispatcher,
      userDispatcher,
    });
  }, [userId, appId, accessToken]);

  // to create DOM elements for appending modal & context menu
  // might fail on server side render
  useEffect(() => {
    const appentElemWithId = (id) => {
      const elem = document.createElement('div');
      elem.setAttribute('id', id);
      const body = document.querySelector('body');
      body.appendChild(elem);
    };
    const modalRoot = 'sendbird-modal-root';
    const menuRoot = 'sendbird-dropdown-portal';
    appentElemWithId(modalRoot);
    appentElemWithId(menuRoot);
  }, []);

  // add-remove theme from body
  useEffect(() => {
    try {
      const body = document.querySelector('body');
      body.classList.add(`sendbird-theme--${theme || 'light'}`);
      // eslint-disable-next-line no-empty
    } catch { }
    return () => {
      try {
        const body = document.querySelector('body');
        body.classList.remove('sendbird-theme--light');
        body.classList.remove('sendbird-theme--dark');
        // eslint-disable-next-line no-empty
      } catch { }
    };
  }, [theme]);
  return (
    <SendbirdSdkContext.Provider
      value={{
        stores: {
          sdkStore,
          userStore,
        },
        dispatchers: {
          sdkDispatcher,
          userDispatcher,
          reconnect: () => {
            handleConnection({
              userId,
              appId,
              accessToken,
              sdkStore,
              nickname,
              profileUrl,
              sdk: sdkStore.sdk,
            }, {
              sdkDispatcher,
              userDispatcher,
            });
          },
        },
        config: {
          userId,
          appId,
          accessToken,
          theme,
          userListQuery,
        },
      }}
    >
      {children}
    </SendbirdSdkContext.Provider>
  );
}

Sendbird.propTypes = {
  userId: PropTypes.string.isRequired,
  appId: PropTypes.string.isRequired,
  accessToken: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
    PropTypes.any,
  ]).isRequired,
  theme: PropTypes.string,
  nickname: PropTypes.string,
  profileUrl: PropTypes.string,
  userListQuery: PropTypes.func,
};

Sendbird.defaultProps = {
  accessToken: '',
  theme: 'light',
  nickname: '',
  profileUrl: '',
  userListQuery: null,
};
