export const createChannel = (sdk, selectedUsers) => new Promise((resolve, reject) => {
  sdk.GroupChannel.createChannelWithUserIds(
    selectedUsers,
    false, // 'distinct' is false
    '', // NAME
    '', // COVER_IMAGE_OR_URL
    '', // DATA
    (response, error) => {
      const swapParams = sdk.getErrorFirstCallback();
      let groupChannel = response;
      let err = error;
      if (swapParams) {
        groupChannel = error;
        err = response;
      }
      if (err) {
        reject(err);
      }
      resolve(groupChannel);
    },
  );
});

export default createChannel;
